import React, { PureComponent } from 'react';
import { Dialog } from '@material-ui/core';

import DialogContent from './dialogSearchFieldComponents/DialogContent';

export default class DialogSearchField extends PureComponent {
  state = {
    anchorEl: null,
    anchorElWidth: '',
  };

  handleFocus = e => {
    e.preventDefault();
    const parentElement =
      e.target.parentElement.parentElement.parentElement;
    this.setState({
      anchorEl: parentElement,
      anchorElWidth: window.innerWidth + 6,
    });
  };

  render() {
    const { open, handleClose, searchField, searchResults } = this.props;
    const { anchorEl, anchorElWidth } = this.state;

    return (
      <Dialog fullScreen open={open} onClose={handleClose} keepMounted>
        <DialogContent
          handleClose={handleClose}
          searchField={searchField}
          searchResults={searchResults}
          handleFocus={this.handleFocus}
          anchorEl={anchorEl}
          anchorElWidth={anchorElWidth}
        />
      </Dialog>
    );
  }
}
