import React, { Fragment } from 'react';
import { Popper, Paper } from '@material-ui/core';

const SearchSelectWrapper = props => {
  const {
    searchResultOpen,
    anchorEl,
    anchorElWidth,
    elevation,
    square,
    padding,
    full,
    children,
  } = props;
  return (
    <Popper
      modifiers={{ flip: { enabled: false } }}
      open={searchResultOpen}
      anchorEl={anchorEl}
      style={{ zIndex: 1301 }}
    >
      <Paper
        elevation={elevation}
        square={square}
        style={{
          marginTop: 3,
          padding: `${padding}`,
          width: `${anchorElWidth}px`,
          overflowY: 'auto',
          maxHeight: full ? '100vh' : '270px',
          minHeight: 0,
        }}
      >
        {children}
      </Paper>
    </Popper>
  );
};

export default SearchSelectWrapper;
