import React, { PureComponent, Fragment } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import {
  ListItem,
  ListItemText,
  withWidth,
  InputAdornment,
} from '@material-ui/core';
import ThreePropsMenu from './searchSelectorComponents/ThreePropsMenu';
import SearchSelectWrapper from './searchSelectorComponents/SearchSelectWrapper';
import AdditionalInfoDialog from './AdditionalInfoDialog';
import SearchIcon from '@material-ui/icons/Search';
import _uniq from 'lodash/uniq';
import DialogSearchField from './DialogSearchField';

/* eslint-disable-next-line no-extend-native */
String.prototype.has = function(value) {
  return this.replace(/, /g, ' ')
    .toLowerCase()
    .includes(value.toLowerCase());
};

/* eslint-disable-next-line no-extend-native */
String.prototype.equals = function(value) {
  return this.toLowerCase() === value.toLowerCase();
};

class SearchSelector extends PureComponent {
  constructor() {
    super();
    this.state = {
      searchInput: '',
      selected: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      anchorEl: null,
      anchorElWidth: '',
      filteredData: [],
      searchDialogOpen: false,
      readOnly: false,
      searchResultOpen: false,
    };
  }

  componentDidMount() {
    const { required } = this.props;
    this.interval = null;
    this.setState({
      validators: required ? ['required'] : [],
      errorMessages: required ? ['จำเป็นต้องกรอก'] : [],
    });

    this.updateInterval = setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  componentWillUnmount() {
    clearTimeout(this.interval);
    clearTimeout(this.updateInterval);
  }

  handleClick = () => {
    this.props.width === 'xs' &&
      this.setState({
        searchDialogOpen: true,
      });
  };

  handleDialogClose = () => {
    this.setState({
      searchDialogOpen: false,
      searchResultOpen: false,
    });
  };

  handleBlur = e => {
    e.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
        searchResultOpen: false,
      }));
    }, 150);
  };

  handleSeachInput = e => {
    e.preventDefault();
    const { value } = e.target;
    const { currentTarget } = e;

    this.setState({
      searchInput: value,
      selected: false,
      anchorEl: currentTarget.parentElement,
      anchorElWidth: currentTarget.parentElement.offsetWidth,
      filteredData: _uniq(this.filterData(value)),
      searchResultOpen: value === '' ? false : true,
    });
  };

  filterByThirdProps = input => {
    const { thirdDataProp, infoData } = this.props;

    const exactOutput = infoData.find(datum =>
      datum[thirdDataProp].equals(input)
    );

    const partialOutput = infoData.filter(datum =>
      datum[thirdDataProp].has(input)
    );

    return exactOutput
      ? Array.from(exactOutput).concat(partialOutput)
      : partialOutput;
  };

  filterByTwoProps = input => {
    const { firstDataProp, secondDataProp, infoData } = this.props;

    const exactOutput = infoData.filter(
      datum =>
        datum[firstDataProp].equals(input) ||
        datum[secondDataProp].equals(input)
    );
    const partialOutput = infoData.filter(
      datum =>
        datum[firstDataProp].has(input) || datum[secondDataProp].has(input)
    );
    return exactOutput.length !== 0
      ? exactOutput.concat(partialOutput)
      : partialOutput;
  };

  filterData = input => {
    const { thirdDataProp } = this.props;

    return thirdDataProp
      ? this.filterByTwoProps(input).concat(this.filterByThirdProps(input))
      : this.filterByTwoProps(input);
  };

  handleSelect = e => {
    e.preventDefault();
    const { maxDigits, required, validators, errorMessages } = this.props;
    this.setState({
      selected: true,
      searchInput: e.currentTarget.innerText.trim().slice(0, maxDigits),
      validators: required ? validators : [],
      errorMessages: required ? errorMessages : [],
      searchDialogOpen: false,
      searchResultOpen: false,
    });
  };

  render() {
    const {
      handleSeachInput,
      handleSelect,
      handleBlur,
      handleClick,
      handleDialogClose,
    } = this;

    const {
      id,
      label,
      helperText,
      placeholder,
      firstDataProp,
      secondDataProp,
      thirdDataProp,
      required,
      width,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
      disabled,
      InputProps,
    } = this.props;

    const {
      searchInput,
      validators,
      errorMessages,
      filteredData,
      searchDialogOpen,
      searchResultOpen,
    } = this.state;

    const SearchField = (
      <TextValidator
        autoComplete="off"
        id={id}
        name={id}
        disabled={disabled}
        fullWidth
        required={required}
        label={label}
        helperText={helperText}
        placeholder={placeholder}
        onBlur={handleBlur}
        onClick={handleClick}
        style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
        value={searchInput}
        validators={validators}
        errorMessages={errorMessages}
        onChange={handleSeachInput}
        variant="outlined"
        margin={width === 'xs' ? 'normal' : 'dense'}
        InputProps={
          InputProps
            ? InputProps
            : {
                readOnly: width === 'xs' ? true : false,
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon fontSize="small" />
                  </InputAdornment>
                ),
                endAdornment: dialogContent && width !== 'xs' && (
                  <InputAdornment position="end">
                    <AdditionalInfoDialog
                      maxWidth={dialogWidth}
                      titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                      titleEN={label}
                      format={dialogFormat}
                    >
                      {dialogContent}
                    </AdditionalInfoDialog>
                  </InputAdornment>
                ),
              }
        }
      />
    );

    const SearchResults = (
      <SearchSelectWrapper
        searchResultOpen={searchResultOpen}
        anchorEl={this.props.anchorEl || this.state.anchorEl}
        anchorElWidth={
          this.props.anchorElWidth || this.state.anchorElWidth
        }
      >
        {filteredData.slice(0, 20).map((datum, i) => (
          <ListItem
            button
            key={i}
            onClick={handleSelect}
            value={datum[firstDataProp]}
          >
            {thirdDataProp ? (
              <ThreePropsMenu
                first={datum[firstDataProp]}
                second={datum[secondDataProp]}
                third={datum[thirdDataProp]}
              />
            ) : (
              <ListItemText
                primary={datum[firstDataProp]}
                secondary={datum[secondDataProp]}
              />
            )}
          </ListItem>
        ))}
      </SearchSelectWrapper>
    );

    return (
      <Fragment>
        {width === 'xs' ? (
          <Fragment>
            <DialogSearchField
              open={searchDialogOpen}
              handleClose={handleDialogClose}
              searchField={SearchField}
              searchResults={SearchResults}
              dialogTitleTH={dialogTitleTH}
              dialogTitleEN={label}
              dialogFormat={dialogFormat}
              dialogContent={dialogContent}
            />
            {SearchField}
          </Fragment>
        ) : (
          <Fragment>
            {SearchField}
            {SearchResults}
          </Fragment>
        )}
      </Fragment>
    );
  }
}

export default withWidth()(SearchSelector);
