import React from 'react';
import { Paper, IconButton, InputAdornment } from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import AdditionalInfoDialog from '../AdditionalInfoDialog';
const DialogContent = props => {
  const {
    handleClose,
    dialogTitleTH,
    dialogTitleEN,
    dialogFormat,
    dialogContent,
    searchField,
    searchResults,
    anchorEl,
    anchorElWidth,
    handleFocus,
  } = props;

  return (
    <Paper
      elevation={1}
      square
      style={{
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        overflowY: 'auto',
      }}
    >
      <IconButton onClick={handleClose}>
        <ArrowBackIcon />
      </IconButton>
      {React.cloneElement(searchField, {
        InputProps: {
          readOnly: false,
          endAdornment: dialogContent && (
            <InputAdornment position="end">
              <AdditionalInfoDialog
                titleTH={dialogTitleTH}
                titleEN={dialogTitleEN}
                format={dialogFormat}
              >
                {dialogContent}
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        },
        autoFocus: true,
        onFocus: handleFocus,
        margin: 'dense',
      })}
      {React.cloneElement(searchResults, {
        anchorEl: anchorEl,
        anchorElWidth: anchorElWidth,
        full: true,
        square: true,
        elevation: 0,
        padding: '0 12px',
      })}
    </Paper>
  );
};

export default DialogContent;
